.intro{
    display: flex;
    align-items: center;
    justify-content: end;
    background-image: url("../../img/introBg.jpg");
    background-repeat:  no-repeat;
    height: 90vh;
    width: 100%;
    overflow: hidden;
}
.introTitle{
    width: 50%;
    font-size: 50px;
    text-align: center;
    line-height: .8;
}
.introTitle h1{
    margin: 30px 0;
    font-size: 100px;
    color: rgb(203,5,5);
}
.introTitle p{
    font-weight: bold;
}
.introBtnLink {
    text-decoration: none;
    color: white;
}
.introBtn{
  width: 40%;
  margin: 10% 20%;
  background: rgb(180, 0, 0);
  border: none;
  color: white;
  letter-spacing: 2px;
  outline: none;
  padding: 20px;
  font-size: 40px;
  cursor: pointer;
  transition: all .5s;
}
.introBtn:hover{
    background-color: rgb(130, 0, 0);
    color: rgb(207, 207, 207);
}
.introBtn:active{
    background-color: rgb(100, 0, 0);
    box-shadow: inset 5px 5px rgb(80, 0, 0);
    color: rgb(80, 0, 0);
}
@media screen and (max-width: 768px) {
    .intro{
        background-size: cover;
        background-repeat:  no-repeat;
        height: 70vh;
    }
    .introTitle{
        width: 30%;
        font-size: 30px;
        text-align: right;
    }
    .introTitle h1{
        font-size: 80px;
    }
    .introTitle > *{
        position: relative;
        right: 25%;
    }
    .introBtn{
        width: 200px;
        font-size: 30px;
        padding: 10px;
        margin-top: 20%;
    }
}
@media screen and (max-width: 480px) {
    .intro{
        background-size: cover;
        height: 50vh;
    }
    .introTitle{
        font-size: 20px;
    }
    .introTitle h1{
        font-size: 60px;
    }
    .introBtn{
        width: 100%;
        font-size: 20px;
        right: 40%;
    }
}