.header{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0 50px;
    width: 100%;
    height: 15vh;
    overflow: visible;
    position: sticky;
    top: 0;
    z-index: 5;
    background: rgb(55, 54, 54);
}
.homeNav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    font-size: 40px;
    font-weight: 700;
}
.navList{
    color: black;
    transition: all 0.3s;
    padding: 10px;
    letter-spacing: 3px;
    border-radius: 1rem;
    list-style-type: none;
    text-decoration: none;
}
.navList:hover{
    color: rgb(203,5,5);
    transform: scale(1.2);
    cursor: pointer;
}
.navList:active{
    color: rgb(156, 4, 4);
    box-shadow: 0 0 20px rgb(156, 4, 4);
}
@media screen and (max-width: 768px) {
    .header{
        max-height: 10vh;
        width: 100%;
    }
    .homeNav{
        gap: 10px;
        font-size: 18px;
        margin: 0;
    }
}
@media screen and (max-width: 480px) {
    .header{
        height: 8vh;
    }
    .list1, .list3{
        display: none;
    }
}