.itemBox{
    position: fixed;
    z-index: 7;
    background-color: rgb(129, 0, 0);
    border-radius: 1rem;
    width: 70%;
    max-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5% 15%;
    padding: 2% 5%;
}
.itemHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.itemTitle{
    font-size: 70px;
    margin: 0;
    width: 50%;
}
.itemCloseBtn{
    border: none;
    background: none;
    font-size: 36px;
    font-weight: bold;
    cursor: pointer;
    transition: all .3s;
}
.itemCloseBtn:hover{
    transform: scale(1.3);
    font-weight: bolder;
}
.item{
    width: 100%;
    margin: 5% 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.itemImg{
    width: 40%;
    box-shadow: 15px 15px 20px;
}
.itemInfo{
    width: 50%;
    margin: 0 5%;
}
.itemDesc{
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 700;
}
.itemPrice{
    font-size: 36px;
    font-family: 'Times New Roman', Times, serif;
}
.itemSizes{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 10% 0;
    gap: 10px;
    font-weight: bold;
}
.size{
    display: flex;
    align-items: center;
    padding: 10px;
    transition: all .5s;
}
.sizeLabel{
    letter-spacing: 3px;
    font-size: 30px;
}
.size:hover{
    transform: scale(1.2);
}
.sizeInput {
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    cursor: pointer;
    background-color: black;
    background-repeat: no-repeat;
}
.sizeInput:checked {
    background-image: radial-gradient( rgb(255, 74, 74) 0%, rgb(197, 31, 31) 10%, rgb(128, 13, 13) 20%, rgb(0, 0, 0, 0) 60% );
}
.addToCartBtn{
    background-color: black;
    opacity: .8;
    color: rgb(128, 13, 13);
    padding: 20px;
    margin: 20px 30px 0 100px;
    border: none;
    border-radius: 1rem;
    font-size: 40px;
    cursor: pointer;
}
.addToCartBtn:hover{
    opacity: 1;
}
@media screen and (max-width: 768px) {
    .itemImg{
        width: 50%;
    }
    .itemDesc{
        font-size: 16px;
    }
    .itemPrice{
        font-size: 20px;
    }
    .sizeLabel{
        font-size: 20px;
    }
    .sizeInput{
        width: 10px;
        height: 10px;
    }
    .addToCartBtn{
        padding: 10px;
        margin: 5% 20%;
        font-size: 20px;
    }
}
@media screen and (max-width: 480px) {
    .itemBox{
        min-height: 90%;
        top: 5%;
    }
    .itemImg{
        position: absolute;
        width: 70%;
        margin: 0 8%;
        top: 13%;
    }
    .itemInfo{
        position: relative;
        width: 100%;
        margin-top: 100%;
    }
    .addToCartBtn{
        margin: 5% 0 0 30%;
        font-size: 24px;
    }
}