.contact{
    margin-top: 50px;
}
.contact > *{
    letter-spacing: 3px;
}
.contact h1{
    text-align: center;
    margin: 50px;
    color: white;
}
.links{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.link {
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
    font-size: 24px;
}
.link img{
    width: 50px;
    margin: 20px;
}
.copyright{
    text-align: center;
    margin: 0;
    background-color: rgb(168, 11, 11);
    font-family: 'Times New Roman', Times, serif;
}
@media screen and (max-width: 480px) {
    .contact h1{
        font-size: 20px;
        margin: 30px 10%;
    }
    .links{
        flex-direction: column;
    }
    .link{
        font-size: 16px;
    }
    .link img{
        width: 30px;
        margin: 10px;
    }
}