.about{
    background-color: rgb(150, 0, 0, .8);
    border-radius: 2rem;
    margin: 100px;
    padding: 30px 100px;
}
.aboutTitle{
    margin: 50px;
    font-size: 36px;
}
.aboutP{
    margin: 50px;
    font-family: 'Times New Roman', Times, serif;
    font-size: 20px;
}
.imgContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px;
    gap: 20px;
    flex-wrap: wrap;
}
.aboutImg{
    width: 20%;
    height: 300px;
    box-shadow: 5px 5px 10px;
    transition: all .5s;
}
.aboutImg:hover{
    transform: scale(1.2);
}
@media screen and (max-width: 768px) {
    .about{
        width: 80%;
        padding: 5%;
        margin: 5% 10%;
    }
    .aboutTitle{
        margin: 0 0 0 10%;
    }
    .aboutP{
        margin: 5% 10%;
        font-size: larger;
    }
    .aboutImg{
        height: 150px;
    }
}
@media screen and (max-width: 480px) {
    .aboutP{
        font-size: 16px;
        margin: 5%;
    }
    .imgContainer{
        margin: 0;
        gap: 10px;
    }
    .aboutImg{
        height: 100px;
    }
}