.shopNav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: sticky;
    top: 0;
    z-index: 5;
    width: 100%;
    background-color: rgb(55, 54, 54);
}
.logo{
    width: 15%;
    margin: 10px 0;
}
.logoImg{
    width: 100%;
}
.cartBtn{
    width: 4%;
    min-width: 50px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: all .5s;
}
.cartBtn:hover{
    opacity: .7;
}
.cartBtn:hover .cartNumber{
    top: 35%;
    right: 21%;
    color: rgb(70, 70, 70);
}
.cartBtnImg{
    width: 100%;
}
.cartNumber{
    transition: all .5s;
    position: absolute;
    background-color: rgb(155, 9, 9);
    color: white;
    border-radius: 50%;
    padding: 5px 10px;
    font-size: 20px;
    font-weight: 700;
    top: 50%;
    right: 20%;
    font-family: 'Times New Roman', Times, serif;
}
.note{
    display: block;
    width: 90%;
    max-height: 80vh;
    overflow: auto;
    background-color: rgb(155, 9, 9);
    padding: 5%;
    margin: 0 5%;
    border-radius: 1rem;
    text-align: center;
}
.note > *{
    font-family: 'Times New Roman', Times, serif;
}
.noteTitle1{
    font-size: 20px;
}
.noteTitle2{
    font-size: 16px;
    margin: 2% 0;
}
.pricelist{
    font-size: 20px;
}
.pricelist > strong{
    font-weight: bolder;
    font-family: 'Times New Roman', Times, serif;
}
.shop{
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 1rem;
    margin: 5%;
}
.search{
    display: flex;
    align-items: center;
    justify-content: end;
}
.searchInput{
    padding: 2%;
    margin: 5%;
    font-size: 20px;
    border: none;
    border-radius: 1rem;
}
.catalog{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 5%;
    margin: 0 5%;
    padding-bottom: 5%;
}
.catalogItem{
    width: 20%;
    text-align: center;
    margin-bottom: 50px;
}
.catalogItemImg{
    width: 100%;
    height: 250px;
    margin: 20px 0;
    box-shadow: 5px 5px 10px;
    transition: all .5s;
}
.catalogItemImg:hover{
    transform: scale(1.2);
}
.catalogItem:hover{
    cursor: pointer;
}
.catalogItemTitle{
    font-size: 35px;
}
.catalogItemPrice{
    font-size: 16px;
    font-family: 'Times New Roman', Times, serif;
}
@media screen and (max-width: 768px) {
    .cartBtn{
        width: 5%;
        min-width: 30px;
    }
    .cartNumber{
        font-size: 10px;
        padding: 2px 5px;
    }
    .note > *{
        font-size: smaller;
    }
    .catalogItemImg{
        height: 200px;
    }
    .catalogItemTitle{
        font-size: 24px;
    }
}
@media screen and (max-width: 480px) {
    .cartNumber{
        right: 19%;
    }
    .searchInput{
        width: 30%;
        font-size: 16px;
    }
    .catalogItem{
        margin-bottom: 30px;
    }
    .catalogItemImg{
        height: 120px;
        margin: 10px 0;
    }
    .catalogItemTitle{
        font-size: 16px;
        margin-bottom: 5px;
    }
    .catalogItemPrice{
        font-size: 10px;
    }
}