.checkoutBox{
    width: 80%;
    padding: 2%;
    margin: 2.5% 10%;
    border-radius: 1rem;
    height: 90vh;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.9);
    position: fixed;
    z-index: 6;
}
.checkoutHeader{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 5%;
}
.form{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.checkoutTitle{
    margin-left: 5%;
    font-size: 60px;
    letter-spacing: 3px;
    color: rgb(138, 0, 0);
}
.checkoutItems{
    width: 30%;
    padding: 10px;
    border: 5px double rgb(138, 0, 0);
}
.checkoutItemsTitle{
    color: rgb(138, 0, 0);
    text-align: left;
    text-decoration: underline;
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
}
.checkoutItem{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 5px;
    border-bottom: 1px solid;
    color: rgb(104, 104, 104);
}
.checkoutItemImg{
    width: 20%;
    border-radius: 1rem;
    box-shadow: 3px 1px 5px black;
}
.info > *{
    font-family: 'Times New Roman', Times, serif;
    font-size: 16px;
}
.inputs{
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5%;
}
.form-input{
    background-color: rgba(140, 0, 0, 0.6);
    border: none;
    border-radius: 1rem;
    font-size: 16px;
    width: 100%;
    font-weight: bold;
    display: block;
    padding: 7%;
    margin: 5%;
    font-family: 'Times New Roman', Times, serif;
}
.message{
    height: 100px;
}
.order{
    position: absolute;
    display: flex;
    align-items: center;
    bottom: -40%;
    padding: 10px;
    font-size: 36px;
    border: none;
    background-color: rgb(138, 0, 0);
    color: white;
    cursor: pointer;
    transition: all .3s;
}
.order:hover{
    background-color: rgb(120, 0, 0);
    box-shadow: 5px 5px 10px rgb(20, 0, 0);
}
.order:active{
    background-color: rgb(100, 0, 0);
    color: rgb(128, 105, 105);
}
.orderLogo{
    width: 50px;
    margin: 0 10px;
}
@media screen and (max-width: 768px) {
    .checkoutBox{
        top: 5%;
        justify-content: space-around;
    }
    .form{
        display: block;
    }
    .checkoutItems{
        width: 80%;
        margin: 0 10%;
    }
    .inputs{
        width: 95%;
        margin: 5% 0;
    }
    .order{
        margin: 0 25%;
    }
}
@media screen and (max-width: 480px) {
    .order{
        width: 80%;
        margin: 0 10%;
        font-size: 45px;
    }
}