.cartBox{
    width: 70%;
    margin: 5% 15%;
    border-radius: 1rem;
    max-height: 80vh;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    z-index: 6;
}
.cartHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 50px;
}
.empty{
    color: rgb(104, 104, 104);
    text-align: center;
    padding: 10% 0;
    font-size: 36px;
    letter-spacing: 2px;
}
.redEmpty{
    color: rgb(165, 11, 11);
}
.cartTitle{
    color: rgb(165, 11, 11);
    font-size: 60px;
    letter-spacing: 3px;
}
.closeBtn{
    background-color: transparent;
    border: none;
    width: 10%;
    color: rgb(104, 104, 104);
    font-size: 36px;
    cursor: pointer;
}
.closeBtn:hover{
    color: rgb(165, 11, 11);
}
.closeBtn:active{
    color: rgba(165, 11, 11, .7);
}
.cartItem{
    width: 90%;
    margin: 0 5%;
    padding: 5px;
    border-bottom: 1px solid;
    color: rgb(104, 104, 104);
}
.cartItemHeader{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10%;
}
.cartItemImg{
    width: 20%;
    border-radius: 1rem;
    box-shadow: 3px 1px 5px black;
}
.cartItemDetails{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.cartItemDetails > *{
    margin: 5% 0;
    font-family: 'Times New Roman', Times, serif;
}
.cartItemTitle{
    font-size: 36px;
    font-family: 'Times New Roman', Times, serif;
}
.cartItemSize{
    font-size: 20px;
}
.quantityControls{
    display: flex;
    align-items: center;
}
.quantity, .quantityDown, .quantityUp{
    width: 50px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    text-align: center;
    color: rgb(104, 104, 104);
    font-size: 20px;
    font-weight: 900;
    background-color: transparent;
    border: 1px solid rgb(104, 104, 104);
    font-family: 'Times New Roman', Times, serif;
}
.quantityUp, .quantityDown{
    cursor: pointer;
    background-color: rgba(165, 11, 11, .7);
    color: rgb(104, 104, 104);
    width: 30px;
}
.quantityUp:hover, .quantityDown:hover{
    background-color: rgb(165, 11, 11);
}
.quantityUp:active, .quantityDown:active{
    background-color: rgb(130, 8, 8);
}
.removeBtn{
    padding: 10px;
    width: 80px;
    font-weight: 900;
    color: rgb(104, 104, 104);
    background-color: transparent;
    border: 1px solid rgb(104, 104, 104);
}
.removeBtn:hover{
    background-color: rgb(104, 104, 104);
    color: rgb(0, 0, 0);
    cursor: pointer;
}
.removeBtn:active{
    background-color: rgb(85, 85, 85);
    color: rgb(0, 0, 0);
}
.totalPrice{
    font-size: 24px;
}
.checkoutBtn{
    margin: 3% 25%;
    width: 50%;
    padding: 10px;
    border: 3px solid rgb(165, 11, 11);
    background: none;
    color: rgb(165, 11, 11);
    font-size: 50px;
    cursor: pointer;
    animation: smoke infinite;
}
.checkoutBtn:active{
    background-color: rgb(165, 11, 11);
    color: black;
}


@media screen and (max-width: 480px) {
    .cartBox{
        max-height: 90vh;
    }
    .cartItemTitle{
        font-size: 20px;
    }
    .quantityDown, .quantityUp{
        width: 10px;
    }
    .totalPrice{
        font-size: 10px;
        text-align: center;
    }
}
@media screen and (max-width: 768px){
    .removeBtn{
        margin: 0 5%;
    }
}